// frontend/src/components/NavBar.tsx
'use client';

import { useState, useEffect } from "react"; // Adicionado useEffect para o tema
import Link from "next/link";
import ApplicationLogo from "./ApplicationLogo"; //
import Dropdown from "./Dropdown"; //
import NavLink from "./NavLink"; //
// Se estiver usando next-themes, o ThemeToggle precisa ser adaptado ou usar um hook do next-themes
// Por enquanto, vou manter o ThemeToggle original, mas ele precisará de ajustes para funcionar com next-themes
import ThemeToggle from "@/components/menu/ThemeToggle"; //
import { usePathname } from "next/navigation"; // Para ativar NavLinks
import { ChevronDown, Menu as MenuIcon, X as XIcon } from 'lucide-react'; // Ícones

// Este componente NavBar.tsx agora será apenas a barra de navegação,
// não o layout da página inteira. O conteúdo da página (children) é gerenciado pelo RootLayout.
export default function NavbarLayout() {
  const user = { // Mock de usuário, substitua pela sua lógica de autenticação
    name: "Fulano de Tal",
    email: "fulano@example.com",
  };

  const [showingNavigationDropdown, setShowingNavigationDropdown] = useState(false);
  const [showUserDropdown, setShowUserDropdown] = useState(false);
  const pathname = usePathname(); // Hook para obter o caminho atual

  const navLinks = [
    { href: "/perguntas", label: "Perguntas" },
    { href: "/questionarios", label: "Questionários" },
    { href: "/queperg", label: "QuePerg" },
    { href: "/avaliacao", label: "Avaliação" },
    { href: "/respostas", label: "Respostas" },
    { href: "/usuAval", label: "Usuário Aval." },
    { href: "/usuario", label: "Usuários" },
  ];

  // Para fechar o dropdown do usuário ao clicar fora
  useEffect(() => {
    const handleClickOutside = (event: MouseEvent) => {
      if (showUserDropdown && !(event.target as HTMLElement).closest('.user-dropdown-trigger')) {
        setShowUserDropdown(false);
      }
    };
    document.addEventListener('mousedown', handleClickOutside);
    return () => {
      document.removeEventListener('mousedown', handleClickOutside);
    };
  }, [showUserDropdown]);


  return (
    <nav className="fixed top-0 left-0 right-0 z-50 bg-element-bg border-b border-main-border shadow-sm">
      <div className="mx-auto max-w-screen-xl px-4 sm:px-6 lg:px-8"> {/* max-w-screen-xl para largura total como na referência */}
        <div className="flex h-16 items-center justify-between">
          {/* Seção Esquerda: Logo e Links Desktop */}
          <div className="flex items-center">
            <Link href="/" className="flex-shrink-0">
              <ApplicationLogo className="block h-8 w-auto text-primary" /> {/* text-primary para o logo */}
            </Link>
            <div className="hidden md:ml-6 md:flex md:space-x-4 lg:space-x-6">
              {navLinks.map((link) => (
                <NavLink key={link.href} href={link.href} active={pathname === link.href}>
                  {link.label}
                </NavLink>
              ))}
            </div>
          </div>

          {/* Seção Direita: ThemeToggle e User Dropdown */}
          <div className="hidden md:flex md:items-center md:space-x-4">
            <ThemeToggle /> {/* Este componente pode precisar ser atualizado para funcionar com next-themes */}
            
            <div className="relative user-dropdown-trigger">
              <button
                type="button"
                onClick={() => setShowUserDropdown((prev) => !prev)}
                className="flex items-center rounded-full p-1 text-text-muted hover:text-primary focus:outline-none focus:ring-2 focus:ring-primary focus:ring-offset-2 focus:ring-offset-element-bg"
                aria-expanded={showUserDropdown ? 'true' : 'false'}
                aria-haspopup="true"
              >
                <span className="sr-only">Abrir menu do usuário</span>
                {/* Idealmente um Avatar aqui, por enquanto o nome */}
                <span className="text-sm font-medium mr-1">{user.name}</span>
                <ChevronDown size={16} aria-hidden="true" />
              </button>

              {showUserDropdown && (
                <div 
                  className="absolute right-0 mt-2 w-56 origin-top-right rounded-md bg-element-bg shadow-lg ring-1 ring-main-border ring-opacity-5 focus:outline-none py-1"
                  role="menu" 
                  aria-orientation="vertical" 
                  aria-labelledby="user-menu-button"
                >
                  <div className="px-4 py-3">
                    <p className="text-sm text-text-base font-medium">{user.name}</p>
                    <p className="text-xs text-text-muted truncate">{user.email}</p>
                  </div>
                  <div className="border-t border-main-border"></div>
                  <Dropdown.Link href="/profile">Meu Perfil</Dropdown.Link>
                  <Dropdown.Link href="/config">Configurações</Dropdown.Link>
                  <button
                    type="button"
                    onClick={() => alert("Fazer logout")} // Substitua por sua lógica de logout
                    className="block w-full text-left px-4 py-2 text-sm text-text-base hover:bg-page-bg hover:text-primary"
                    role="menuitem"
                  >
                    Sair
                  </button>
                </div>
              )}
            </div>
          </div>

          {/* Botão Mobile para abrir menu */}
          <div className="-mr-2 flex items-center md:hidden">
            <button
              type="button"
              onClick={() => setShowingNavigationDropdown(!showingNavigationDropdown)}
              className="ml-2 inline-flex items-center justify-center rounded-md p-2 text-text-muted hover:bg-page-bg hover:text-primary focus:outline-none focus:ring-2 focus:ring-primary"
              aria-controls="mobile-menu"
              aria-expanded={showingNavigationDropdown ? 'true' : 'false'}
            >
            
              <span className="sr-only">Abrir menu principal</span>
              {showingNavigationDropdown ? (
                <XIcon className="block h-6 w-6" aria-hidden="true" />
              ) : (
                <MenuIcon className="block h-6 w-6" aria-hidden="true" />
              )}
            </button>
          </div>
        </div>
      </div>

      {/* Menu Dropdown Mobile */}
      {showingNavigationDropdown && (
        <div className="md:hidden border-t border-main-border bg-element-bg" id="mobile-menu">
          <div className="space-y-1 px-2 pb-3 pt-2 sm:px-3">
            {navLinks.map((link) => (
              <NavLink
                key={link.href}
                href={link.href}
                active={pathname === link.href}
                className="block rounded-md px-3 py-2 text-base font-medium" // Estilo de bloco para mobile
              >
                {link.label}
              </NavLink>
            ))}
          </div>
          <div className="border-t border-main-border pb-3 pt-4">
            <div className="flex items-center px-4">
              {/* Avatar do usuário aqui */}
              <div className="ml-3">
                <div className="text-base font-medium text-text-base">{user.name}</div>
                <div className="text-sm font-medium text-text-muted">{user.email}</div>
              </div>
            </div>
            <div className="mt-3 space-y-1 px-2">
              <NavLink href="/profile" className="block rounded-md px-3 py-2 text-base font-medium">
                Meu Perfil
              </NavLink>
              <NavLink href="/config" className="block rounded-md px-3 py-2 text-base font-medium">
                Configurações
              </NavLink>
              <button
                type="button"
                onClick={() => alert("Fazer logout")}
                className="block w-full text-left rounded-md px-3 py-2 text-base font-medium text-text-base hover:bg-page-bg hover:text-primary"
              >
                Sair
              </button>
            </div>
          </div>
        </div>
      )}
    </nav>
  );
}